<template>
    <div>
    <loader :is-visible="isLoading"></loader>
    <StorageBar :type="'Doc'"></StorageBar>
    <div class="listing">
        <div class="table-responsive table-fix-header" id="div_forListView">
            <table class="table table-bordered mt-0" style="width: 100%;" id="tblFileList">
                <thead class="thead-bg">
                    <tr>
                        <th style="width:50px !important;" class="text-center"><input id="chkAll" class="chkitems" value=""
                                type="checkbox" v-on:change="onChange()">
                        </th>
                        <th>
                            <span>User Name</span>
                        </th>
                        <th>
                            <span>Designamtion</span>
                        </th>
                        <th>
                            <span>Department</span>
                        </th>
                        <th>
                            <span>Status</span>
                        </th>
                        <th>
                            <span>Consumed Stoarage (MB)</span>
                        </th>
                        <th>
                            <span>Allocated Storage (MB)</span>
                        </th>
                        <th data-width="5%" class="text-center nonresize">
                            <span>Action</span>
                        </th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item,index) in UserData" :key="index">
                        <td class="text-center"><input :Id="item.USER_ID" v-bind:value="item.USER_ID" class="chkItems"
                                type="checkbox" v-on:change="onChange()"></td>
                        <td>
                            <span class="text-overflow-dynamic-container">
                                {{ item.USER_NAME }}
                            </span>
                        </td>
                        <td>
                            <span class="text-overflow-dynamic-container">
                                {{ item.DESIGNATION_NAME }}
                            </span>
                        </td>
                        <td>
                            <span class="text-overflow-dynamic-container">
                                {{ item.DEPARTMENT_NAME }}
                            </span>
                        </td>
                        <td>
                            <span title="Active"
                                class="fullPaid btn d-inline-block w-80 text-center text-truncate btn-success"
                                v-if="item.STATUS_ID == '1001'"> Active</span>

                            <span title="Approved"
                                class="sent bg-secondary btn d-inline-block w-80 text-center text-truncate text-white"
                                v-if="item.STATUS_ID == '1084'"> Approved</span>

                            <span title="Rejected" class="stop btn d-inline-block w-80 text-center text-truncate btn-danger"
                                v-if="item.STATUS_ID == 'Rejected'"> Rejected</span>

                            <span title="Deleted"
                                class="stop px-2 bg-primary btn d-inline-block w-80 text-center text-truncate"
                                v-if="item.STATUS_ID == '1003'"> Deleted</span>

                            <span title="Inactive"
                                class="fullPaid btn d-inline-block w-80 text-center text-truncate btn-danger"
                                v-if="item.STATUS_ID == '1002'">Inactive</span>

                            <span title="Pending"
                                class="fullPaid btn d-inline-block w-80 text-center text-truncate btn-secondary"
                                v-if="item.STATUS_ID == '1005'"> Pending</span>

                            <span title="ActivationAwaited"
                                class="fullPaid btn d-inline-block w-80 text-center text-truncate btn-warning"
                                v-if="item.STATUS_ID == 'ActivationAwaited'"> ActivationAwaited</span>
                        </td>
                        <td>
                            <span class="text-right text-overflow-dynamic-container">
                                {{ item.CONSUMED_STROAGE }}
                            </span>
                        </td>
                        <td>
                            <span class="text-overflow-dynamic-container">
                                <input type="text" asp-for="item.MaxStorage" :id="'idMaxStorage' + item.USER_ID" step="0.01"
                                    maxlength="13" v-model="item.ALLOWED_STORAGE"
                                    class="maxAllowedStorage  form-control text-right disabled" />
                                <!-- :value="item.ALLOWED_STORAGE" -->
                            </span>
                        </td>
                        <td class="text-center single-action">

                            <a class="aEdit" title="Edit" v-if="hideEditIcon" :id="'idEdit' + item.USER_ID"
                                @click="editAllowedStorage(item.USER_ID)"><i
                                    class="fa fa-pencil text-success action-icon "></i></a>
                            <div :id="'divAction' + item.USER_ID" style="display: none;">

                                <a class="aCancel mr-2" :id="'idCancel_' + item.USER_ID" :value="item.USER_ID"
                                    title="Cancel" @click="CancelAction(item.USER_ID, item.ALLOWED_STORAGE)"
                                    href="javascript:;"><i class="fa fa-times-circle text-danger action-icon"></i></a>
                                <a class="aSave" title="Save" :value="item.USER_ID" :id="'idSave_' + item.USER_ID"
                                    href="javascript:;" @click="SaveData(item.USER_ID, item.ALLOWED_STORAGE,item.CONSUMED_STROAGE)"><i
                                        class="fa fa-save text-primary action-icon"></i></a>
                            </div>
                        </td>
                    </tr>
                    <tr v-if="NoRecordLength == 0">
                        <td colspan="7" class="text-danger text-center no-record">No Record Found</td>
                    </tr>
                    <tr v-if="NoRecordLength > 0">
                        <td colspan="2" class="text-right fr font-weight-bold">Total</td>
                        <td colspan="4" class="text-right fr font-weight-bold">{{ totalConsumedStorage }} MB </td>
                        <td class=" text-right fr font-weight-bold">{{ totalAllowedStorage }} MB</td>
                    </tr>
                </tbody>

            </table>
            <!-- <Pager v-bind:numberofrecords="totalRecords" prev-text="Prev"
                                        v-on:change="pageSizecommon" first-text="First" last-text="Last"
                                        next-text="Next" @click="pagerMethod"></Pager> -->
        </div>
    </div>
    <div class="col-lg-12 text-right mt-3 float-right">
        <button title="Previous" @click="PreviousButton()" class="btn btn-warning float-left">Previous</button>
        <button title="Save" @click="SaveButton()" class="btn btn-success"><em aria-hidden="true"
                class="fa fa-save pr-2"></em>
            Save</button>

    </div>
</div>
</template>
<script>
import Pager from '../../components/Common/Pager.vue';
import DataService from '../../services/DataService';
import StorageBar from './StorageBar.vue'
export default {
    components: {
        Pager,
        StorageBar
    },
    data() {
        return {
            isLoading: false,
            UserData: [],
            DepartmentData: [],
            designationData: [],
            userStorageDetailSummary: [],
            pageNo: 1,
            searchCondition: null,
            sortExp: null,
            sortBy: null,
            pageSize: 10,
            totalRecords: '',
            dropDivShow: false,
            showByNameDiv: false,
            showByConsumedDiv: false,
            divDesignationShow: false,
            hideEditIcon: true,
            isAllocatedSpaceOpen: false,
            UserStorageValue: null,
            UserId: null,
            saveStorage: null,
            StorageValue: null,
            jsonobj: [],
            JSONSearchData: [],
            departmentList: null,
            designationList: null,
            txtSearchUserName: "",
            txtConsumedStorage: "",
            userFilterData: [],
            CheckIfExistDepartment: [],
            jsonFilteredData: [],
            label: false,
            totalConsumedStorage: 0,
            totalAllowedStorage: 0,
            NoRecordLength: 0,
            isDeptSearchOn: false,
            isDesgSearchOn: false,
            arrayDepartmentChecks: [],
            departmentSelectedArray: [],
            designationSelectedArray: [],
            arrayDesignationChecks: [],
            CheckIfExistDesignation: [],
            CheckIfExist: [],
            txtDepartmentName: [],
            tempArray: [],
            txtSearchDeptValue: '',
            containerSize: null,
            docStorageMaxLimit: null,
            AllowedClientStorage:null,
            AllowedUserStorage:null,
            total:null,
            consumed:null,
            allocated:null,
            jsonObjData: []
        }
    },
    created() {
        var vm = this;
        vm.FetchData();
        vm.GetDDLDesignationData();
        vm.GetDDLDepartmentData();
        vm.GetStoragedata();
    },
    computed: {
            AvailableSpace(){
                return this.type=='All'?(this.total>=this.consumed?this.total-this.consumed:0):(this.AllowedClientStorage + this.AllowedUserStorage < this.allocated? this.allocated - this.AllowedClientStorage - this.AllowedUserStorage:0);
            },
            AllowedStorage(){
                var val=this.AllowedClientStorage + this.AllowedUserStorage;
                return val;
            },
                
          
        },
    methods:
    {
        validate:function(){
            var vm=this;
            var AllowedClientStorage=vm.AllowedClientStorage;
            var AllowedUserStorage=vm.AllowedUserStorage;
            var available=vm.AvailableSpace;
            var totalspace=vm.totalAllowedStorage;
            var calculate=AllowedUserStorage + available;
            if(calculate<totalspace){
                vm.ShowAlert(vm.$t("You cannot assign space more than available space"), "failure", true, vm.$t("Alert"));
                return false;
            }
            else{
                return true;
            }
        },
        PreviousButton: function () {
            var vm = this;
            vm.$parent.ShowNextTab(3);
        },
        searchDept: function () {
            var vm = this;
            if (vm.txtSearchDeptValue != '') {
                vm.DepartmentData = vm.DepartmentData.filter(usr => usr.name.toLowerCase().includes(vm.txtSearchDeptValue));
            }

        },

        ClearFilter: function (ths, deptId, dept) {

        },
        enableDepartmentSearch: function () {
            var vm = this;
            if ($('.DepartmentchkItems:checked').length > 0) {
                vm.isDeptSearchOn = true;
                vm.departmentSelectedArray = [];
                $('.DepartmentchkItems:checked').each(function () {
                    var deptId = $(this).val();
                    vm.arrayDepartmentChecks = vm.DepartmentData.filter(dept => dept.value == deptId)
                    vm.departmentSelectedArray = vm.departmentSelectedArray.concat(vm.arrayDepartmentChecks)

                });

            }
            else {
                vm.isDeptSearchOn = false;
            }

        },

        enableDesignationSearch: function () {

            var vm = this;
            if ($('.DesignationchkItems:checked').length > 0) {
                vm.isDesgSearchOn = true;
                vm.designationSelectedArray = [];
                $('.DesignationchkItems:checked').each(function () {
                    var desigId = $(this).val();
                    vm.arrayDesignationChecks = vm.designationData.filter(desig => desig.value == desigId)
                    vm.designationSelectedArray = vm.designationSelectedArray.concat(vm.arrayDesignationChecks)

                });

            }
            else {
                vm.isDesgSearchOn = false;
            }

        },
        SkipButton: function () {
            var vm = this;     
            vm.$parent.ShowNextTab(5);
        },
        SaveButton:function(){
            
            var vm =this; 
          vm.jsonObjData = [];
          let formData = new FormData();
         if($('#tblFileList tr').length!=0){
            var params = `pageNo=${vm.pageNo}&pageSize=${vm.pageSize}&sortBy=&sortExp=&userTypeIds=1,2,3`;
            DataService.GetUserStorageDetail(params).then(function (response) {
                vm.UserData = JSON.parse(response.data).USERSTORAGEDETAILLIST;
             if (vm.UserData != null) {
                vm.UserData.forEach((item, index) => {
                    let objData = {
                        DataId: item.USER_ID,
                        DataValue: item.ALLOWED_STORAGE,
                    }
                    vm.jsonObjData.push(objData)
                });
            }
            let userSetting=[{
                    refCode: 'UserSetting',
                    moduleName: 'DocStorage',
                }]
                var configData="configData"
             var conditionData="conditionData"
            var params="[{"+JSON.stringify(configData)+":"+JSON.stringify(vm.jsonObjData)+", "+JSON.stringify(conditionData)+":"+JSON.stringify(userSetting)+"}]"
                if(vm.validate()==true){
                 
                     
                     DataService.ManageDocUserStorageSettingData(JSON.stringify(params))
                    .then((response) => {                                           
                        if (response.data == 0) {
                            vm.$parent.ShowNextTab(5);  
                        }
                        else {
                            vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                        }
                    });
                }
               else{
                vm.ShowAlert(vm.$t("You cannot assign space more than available space"), "failure", true, vm.$t("Alert"));
               }

            });
           
         }
         else{
            vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
         }
        },
        disableDepartmentButton: function () {
            var vm = this;
            vm.isDeptSearchOn = false;
        },

        disableDisgnationButton: function () {
        
            var vm = this;
            vm.isDesgSearchOn = false;
        },

        SearchUserStorage: function () {
           
            var vm = this;
            vm.userFilterData = [];
            $('.DepartmentchkItems:checked').each(function () {
                var deptId = $(this).val();
                vm.CheckIfExistDepartment = vm.UserData.filter(dept => dept.DEPARTMENT_ID == deptId)
                vm.JSONSearchData = Array.from(vm.CheckIfExistDepartment);
                vm.userFilterData = vm.userFilterData.concat(vm.JSONSearchData)
                if (vm.departmentSelectedArray.length > 0) {
                    vm.dropDivShow = true;
                }
                else {
                    vm.dropDivShow = false;
                }

            });

            $('.DesignationchkItems:checked').each(function () {
                if (vm.userFilterData.length == 0) {
                    vm.jsonFilteredData = vm.UserData;
                }
                var designId = $(this).val();
                vm.CheckIfExistDesignation = vm.UserData.filter(desig => desig.DESIGNATION_ID == designId);
                vm.JSONSearchData = Array.from(vm.CheckIfExistDesignation);
                vm.userFilterData = vm.userFilterData.concat(vm.JSONSearchData)
                if (vm.designationSelectedArray.length > 0) {
                    vm.divDesignationShow = true;
                }
                else {
                    vm.divDesignationShow = false;
                }

            });
            if (vm.txtSearchUserName != "") {
                if (vm.userFilterData.length == 0) {
                    vm.jsonFilteredData = vm.UserData;
                }
                vm.CheckIfExist = vm.UserData.filter(usr => usr.USER_NAME.toLowerCase().includes(vm.txtSearchUserName));
                vm.JSONSearchData = Array.from(vm.CheckIfExist);
                vm.userFilterData = vm.userFilterData.concat(vm.JSONSearchData)
            }
            if (vm.txtConsumedStorage != "") {
                if (vm.userFilterData.length == 0) {
                    vm.jsonFilteredData = vm.UserData;
                }
                vm.CheckIfExist = vm.jsonFilteredData.filter(function (el) {
                    return el.CONSUMED_STROAGE == vm.txtConsumedStorage;
                });
                vm.JSONSearchData = Array.from(vm.CheckIfExist);
                vm.userFilterData = vm.userFilterData.concat(vm.JSONSearchData)
            }
            vm.UserData = vm.userFilterData;
            vm.getTotalStorageData();
        },


        ResetButton: function () {
            var vm = this;
            vm.FetchData();
            vm.GetDDLDesignationData();
            vm.GetDDLDepartmentData();
            vm.dropDivShow = false;
            vm.divDesignationShow = false;
            vm.showByNameDiv = false;
            vm.showByConsumedDiv = false;
            $('.DepartmentchkItems').prop("checked", false);
            $('.DesignationchkItems').prop("checked", false);
            vm.txtSearchUserName = '';
            vm.txtConsumedStorage = '';
            vm.toggleExpandCollapseButton(vm.label = true);
            vm.userFilterData = [];
            vm.designationSelectedArray = [];
            vm.departmentSelectedArray = [];

        },

        toggleExpandCollapseButton: function () {
            var vm = this;
            if (vm.label == false) {
                vm.dropDivShow = true;
                vm.divDesignationShow = true;
                vm.showByNameDiv = true;
                vm.showByConsumedDiv = true;
                vm.label = true;
            }
            else {
                vm.dropDivShow = false;
                vm.divDesignationShow = false;
                vm.showByNameDiv = false;
                vm.showByConsumedDiv = false;
                vm.label = false;
                vm.isDeptSearchOn = false;
                vm.isDesgSearchOn = false;
            }
            vm.txtSearchUserName = "";
            vm.txtConsumedStorage = "";


        },

        getTotalStorageData: function () {
            var vm = this;
            vm.totalAllowedStorage = 0;
            vm.totalConsumedStorage = 0;
            vm.UserData.forEach(x => {
                vm.totalAllowedStorage += x.ALLOWED_STORAGE;
                vm.totalConsumedStorage += x.CONSUMED_STROAGE;
            });


        },

        AllocatedSpacePopup: function () {

            var vm = this;
            var id='';
            var values = ''
            $('#tblFileList .chkItems:checked').not("[id^='chkAll']").each(function () {
                values = $(this).closest('tr').find('.maxAllowedStorage').val();
                 id=$(this).val();
                
                let objData = {
                    Id: id,
                    value:values
                }
                vm.jsonobj.push(objData)
            })
            vm.isAllocatedSpaceOpen = true;
        },

        closeAllocatedSpace: function () {
            var vm = this;
            vm.isAllocatedSpaceOpen = false;
        },
        
        GetStoragedata: function () {
             
                var vm = this;
                var vm = this;
                 DataService.GetStorageReport().then(function (response) {
                    vm.total = response.data[0].CONTAINER_LIMIT;
                    vm.consumed = response.data[0].CONTAINER_SIZE;
                    vm.allocated =  response.data[0].DOC_STORAGE_MAXLIMIT;
                });
                    var params = `pageNo=${1}&pageSize=${10}&sortBy=&sortExp=&userTypeIds=4`;
                        DataService.GetUserStorageDetail(params).then(function (response) {
                        var ClientData = JSON.parse(response.data).USERSTORAGEDETAILLIST;
                        vm.AllowedClientStorage = 0;
                        ClientData.forEach(x => {
                            vm.AllowedClientStorage += x.ALLOWED_STORAGE;
                        })
                    });

                    var userParams = `pageNo=${1}&pageSize=${10}&sortBy=&sortExp=&userTypeIds=1,2,3`;
                    DataService.GetUserStorageDetail(userParams).then(function (response) {
                    
                     var UserData = JSON.parse(response.data).USERSTORAGEDETAILLIST;

                     vm.AllowedUserStorage = 0;
                      UserData.forEach(x => {
                       vm.AllowedUserStorage += x.ALLOWED_STORAGE;
                      })
                    });

            },

        // Save data on save icon 
        SaveData: function (id, allowed,consumed) {
     
            var vm = this
            vm.isLoading = true;
            vm.jsonObjData=[];
            vm.UserId = id,
                vm.UserStorageValue = allowed;
            let objData = {

                DataId: vm.UserId,
                DataValue: vm.UserStorageValue
            }
            vm.jsonObjData.push(objData)
            let userSetting=[{
                    refCode: 'UserSetting',
                    moduleName: 'DocStorage',
                }]
            var configData="configData"
             var conditionData="conditionData"
            var params="[{"+JSON.stringify(configData)+":"+JSON.stringify(vm.jsonObjData)+", "+JSON.stringify(conditionData)+":"+JSON.stringify(userSetting)+"}]"
                //vm.jsonObjData.push(userSetting);
                //var params=JSON.stringify(vm.jsonObjData);
            //var params = `jsonData=${jsonObj}&refCode=UserSetting&moduleName=DocStorage`;
        if(allowed<consumed){
            vm.ShowAlert(vm.$t("Allocate Storage cannot be less than consumed Storage"), "failure", true, vm.$t('Alert'));
        }
        else if(isNaN(allowed)){
            vm.ShowAlert(vm.$t("Allocate Storage must be numeric."), "failure", true, vm.$t('Alert'));
        }
         else{
            DataService.ManageDocUserStorageSettingData(JSON.stringify(params)).then((response) => {

                if (response.data == 0) {
                    this.ShowAlert("Storage Configuration Data Updated Successfully")
                    $('#idMaxStorage' + id).addClass('disabled');
                    $('#divAction' + id).hide();
                    $('#idEdit' + id).show();
                    vm.FetchData();
                    vm.GetDDLDesignationData();
                    vm.GetDDLDepartmentData();
                    vm.$parent.GetStorageSetupStatusDetail();
                }
                else {
                    vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                }
            })  
        }
            vm.isLoading = false;
        },

        CancelAction: function (id) {
            var vm = this;
            $('#idMaxStorage' + id).addClass('disabled');
            $('#idEdit' + id).show();
            $('#divAction' + id).hide();
            vm.FetchData();
            vm.GetDDLDesignationData();
            vm.GetDDLDepartmentData();
        },

        editAllowedStorage: function (id) {
            var vm = this;
            $('#idMaxStorage' + id).removeClass('disabled');
            $('#idEdit' + id).hide();
            $('#divAction' + id).show();

        },
        //Pagination
        PagerData: function (PageNo, sortBy, sortExp, pageSize) {
            // 
            var vm = this;
            vm.pageNo = PageNo;
            vm.pageSize = pageSize;
            vm.FetchData(vm.pageNo, sortBy, sortExp, vm.pageSize);
        },
        // To pass variable 
        pagerMethod: function (e) {
            ;
            if (e.target.innerHTML == 'Prev') {
                this.pageNo = this.pageNo - 1;
            }
            else if (e.target.innerHTML == 'Next') {
                this.pageNo = parseInt(this.pageNo) + 1;
            }
            else {
                this.pageNo = e.target.innerHTML;
            }
            if (this.pageNo == "First") {
                this.pageNo = "1";
            }
            if (this.pageNo == "Last") {
                this.pageNo = this.TotalPages
            }
            if (parseInt(this.pageNo) > 0) {
                this.PagerData(this.pageNo, '', '', this.pageSize)
            }
            else {
                this.pageNo = 1
            }
        },
        pageSizecommon: function (e) {

            var vm = this;
            vm.pageNo = 1;
            vm.pageSize = e.target.value
            vm.PagerData(vm.pageNo, '', '', vm.pageSize)
        },
        onChange: function () {
            var checkCount = $('.chkItems:checked').not("[id^='chkAll']").length;
            if (checkCount > 0) {
                $('#btnStorageSpace').removeClass('disabled');
            }
            else {
                $('#btnStorageSpace').addClass('disabled');
            }
        },
        GetDDLDepartmentData: function () {

            var vm = this;
            let params = {};
            params["type"] = "DOC_TAG";
            var param = "moduleName=DocStorage&type=DEPARTMENT"
            DataService.GetDDLDatas(param).then((response) => {

                vm.DepartmentData = response.data.DATA;
                // vm.DepartmentData.forEach((item, index) => {
                //     vm.Value = item.value;
                //     vm.first_value = item.name
                //     vm.Options.push({
                //         value: vm.Value,
                //         label: vm.first_value
                //     })
                // });
            })
        },
        GetDDLDesignationData: function () {

            var vm = this;
            let params = {};
            params["type"] = "DOC_TAG";
            var param = "moduleName=DocStorage&type=HR_DESIG"
            DataService.GetDDLDatas(param).then((response) => {

                vm.designationData = response.data.DATA;
                // vm.DepartmentData.forEach((item, index) => {
                //     vm.Value = item.value;
                //     vm.first_value = item.name
                //     vm.Options.push({
                //         value: vm.Value,
                //         label: vm.first_value
                //     })
                // });
            })
        },
        FetchData: function () {
            var vm = this;
            vm.isLoading = true;
            var params = `pageNo=${vm.pageNo}&pageSize=${vm.pageSize}&sortBy=&sortExp=&userTypeIds=1,2,3`;
            DataService.GetUserStorageDetail(params).then(function (response) {

                vm.UserData = JSON.parse(response.data).USERSTORAGEDETAILLIST;
                vm.totalRecords = vm.UserData[0].totalRecords
                vm.userStorageDetailSummary = JSON.parse(response.data).USERSTORAGEDETAILSUMMARY;

                vm.getTotalStorageData();
                vm.isLoading = false;
                if (vm.UserData == null) {
                    vm.NoRecordLength = 0;
                }
                else {
                    vm.NoRecordLength = vm.UserData.length;
                }
            })
            setTimeout(function () { vm.ResponsiveDataTable('tblFileList') }, 500);
            $('#btnStorageSpace').addClass('disabled')

        },
    }
}

</script>